.form-creator .svd_survey_designer {
  max-width: unset;
}
body .sv_main .sv_container .sv_body .sv_p_root .sv_q input[type='radio'],
.sv_main .sv_container .sv_body .sv_p_root .sv_q input[type='checkbox'] {
  position: unset;
  left: unset;
  border: 1px solid var(--light-grey);
}
body .sv_main .sv_container .sv_body .sv_p_root .sv_q input[type='radio'] {
  border-radius: 100% !important;
}
body
  .sv_main
  .sv_container
  .sv_body
  .sv_p_root
  .sv_q
  input[type='radio']:checked:after {
  margin: auto;
  margin-top: 3.5px;
  width: 9px;
  height: 9px;
  border-radius: 100%;
  background-color: var(--primary);
  content: '';
  display: block;
}
body
  .sv_main
  .sv_container
  .sv_body
  .sv_p_root
  .sv_q
  input[type='checkbox']:checked:after {
  background-color: var(--primary);
  color: var(--white);
  margin: auto;
  content: '\2714';
  text-align: center;
  display: block;
  border: unset;
}

body .form-creator .svd_container {
  font-size: 13px;
  font-family: var(--font-family-avenirmedium);
}
.form-creator .svd_container.sv_modern_css .svd-page.svd_selected_page {
  background-color: var(--white);
}
.form-creator .svd_container .svd-page {
  height: unset;
}
.noUi-base .noUi-connect {
  background: var(--primary);
}

.svd_container .sv_main .sv_container .sv_body .sv_p_root > .sv_row .sv_q input[type='checkbox'],
.svd_container .sv_main .sv_container .sv_body .sv_p_root > .sv_row .sv_q input[type='radio'] {
  height: 1.2em;
  line-height: normal;
}
.svd_container .sv_main .sv_container .sv_body .sv_p_root > .sv_row .sv_q input[type='checkbox']:focus,
.svd_container .sv_main .sv_container .sv_body .sv_p_root > .sv_row .sv_q input[type='radio']:focus {
  outline: none;
}
.svd_container .sv_main .sv_container .sv_body .sv_p_root > .sv_row .sv_q input[type='radio'].sv-visuallyhidden {
  height: 0px !important;
  outline: none;
}
