:root {
	--primary: #44b07b;
	--danger: red;
	--black: #111111;
	--dark-grey: rgba(97, 93, 93, 0.5);
	--grey: #808080;
	--light-grey: #999999;
	--blur-white: #bdb4b4;
	--lightest-grey: #e9e9e9;
	--white: #fff;
	--font-size: 13px;
	--font-style: normal;
	--font-variant: normal;
	--letter-spacing: normal;
	--font-family-gilroySemi: 'Gilroy-SemiBold';
	--font-family-avenirmedium: AvenirMedium;
	--font-family-bigCaslon: 'BigCaslon-Medium';
}

/* global */
.sv-body__page {
	margin-bottom: 60px;
}

.sv-root-modern {
	width: 90%;
	margin: 0 auto;
	text-align: left;
}

/* elements */
.sv-header__text {
	text-align: center;
}
.sv-header__text span {
	font-family: var(--font-family-bigCaslon);
	font-size: 44px;
	color: var(--black);
	margin-top: 0px;
	margin-bottom: 30px;
}
.sv-form-control {
	font-family: var(--font-family-avenirmedium);
	color: var(--black);
	font-size: 13px;
	font-style: normal;
	font-variant: normal;
	letter-spacing: normal;
}
.sv-title-text {
	font-weight: normal;
	font-size: 13px;
	width: 100%;
	margin-bottom: 15px;
	color: var(--black);
}
.sv-selectbase__item {
	margin-bottom: 1em;
	vertical-align: middle;
}
.sv-selectbase__item .sv-selectbase__label {
	display: flex;
	align-items: center;
}
.sv-question__header--location--top {
	margin-bottom: 2em;
}
.sv-question__header--location--top h5 {
	margin-block-start: 0;
	margin-block-end: 0;
	margin-inline-start: 0px;
	margin-inline-end: 0px;
}
.sv-panel .sv-row:not(:last-child) {
	padding-bottom: 1.875em;
	margin-bottom: 1.875em;
}
.sv-panel__title {
	color: white;
	font-family: var(--font-family-gilroySemi);
	font-size: 16px;
	text-indent: 10px;
	padding-top: 13px;
	padding-bottom: 13px;
	background: var(--primary);
}
.sv-item__control-label {
	padding-top: 0;
	width: 100%;
}
body .sv-root-modern ::-webkit-scrollbar-thumb {
	background: var(--primary);
}
.sv-question__required-text,
.sv-question__num {
	display: none;
}
.sv-table__cell {
	min-width: 18.5em;
}
.sv-table .sv-radio-group-wrapper .sv-radio-group-item,
.sv-table .sv-checkbox-wrapper .sv-checkbox-group-item {
	flex-basis: unset;
	margin-bottom: 0.8em;
	margin-right: 0.8em;
}
